}
.alert.alert-info {
  border-color: #21a4df;
  background-color: rgba(33, 164, 223, 0.2);
}
.alert.alert-warning {
  border-color: #ffc107;
  background-color: rgba(255, 193, 7, 0.2);
}
.alert.alert-danger {
  border-color: #dc3545;
  background-color: rgba(220, 53, 69, 0.2);
}
.alert > p:last-child,
.alert > ol:last-child,
.alert > ul:last-child,
.alert > pre:last-child,
.alert > .pre:last-child {
  margin-bottom: 0;
}

